import { Component, Element, Event, EventEmitter, Host, Prop, h } from '@stencil/core';
import Choices from 'choices.js';
import { SharedStyles } from '../../utils/shared-styles';

@Component({
  tag: 'storm-tag',
  styleUrl: 'storm-tag.css',
  shadow: true,
})
export class StormTag {
  @Element() host: HTMLElement;

  @Prop() class: string;
  @Prop() label: string;
  @Prop({ mutable: true }) value: string[];

  @Event() change: EventEmitter;

  componentWillLoad() {
    SharedStyles.applyShadowRootStyles(this.host);
  }

  componentDidLoad() {
    const element = this.host.shadowRoot.querySelector('input');
    new Choices(element, {
      searchEnabled: false,
      removeItemButton: true,
      classNames: {
        containerOuter: 'choices',
        containerInner: 'choices__inner',
        input: 'choices__input',
        inputCloned: 'choices__input--cloned',
        list: 'choices__list',
        listItems: 'choices__list--multiple',
        listSingle: 'choices__list--single',
        listDropdown: 'choices__list--dropdown',
        item: 'choices__item',
        itemSelectable: 'choices__item--selectable',
        itemDisabled: 'choices__item--disabled',
        itemChoice: 'choices__item--choice',
        placeholder: 'choices__placeholder',
        group: 'choices__group',
        groupHeading: 'choices__heading',
        button: 'choices__button',
        activeState: 'is-active',
        focusState: 'is-focused',
        openState: 'is-open',
        disabledState: 'is-disabled',
        highlightedState: 'is-highlighted',
        selectedState: 'is-selected',
        flippedState: 'is-flipped',
        loadingState: 'is-loading',
        noResults: 'has-no-results',
        noChoices: 'has-no-choices',
      },
    });
    this.value?.push(element.value);
    this.change.emit(this.value);
  }

  handleChange = event => {
    this.value = event.target.value;
    this.change.emit(this.value);
    event.preventDefault();
  };

  render() {
    return (
      <Host>
        <div class={this.class}>
          <label class="input-group-static form-label">{this.label}</label>
          <input class="form-control" id="" type="text" data-color="dark" value={this.value} onChange={e => this.handleChange(e)} />
        </div>
      </Host>
    );
  }
}
